import Mock from 'mockjs';
import loginAPI from './login';
import lossOrderAPI from './loss-order';

Mock.setup({
    //  Ajax 
    timeout: '350-600'
});

// 
Mock.mock(/\/login\/login\/UserLogin/, 'post', loginAPI.loginIn);

