<template>
    <div ref="wrapper" class="ui-scroll">
        <div class="scroll-content">
            <slot></slot>
            <div ref="pullup" class="ui-scroll__pullup" v-if="pullUpLoad">
                <div class="before-trigger" v-if="!isPullUpLoad">
                    <span>{{pullUpTxt}}</span>
                </div>
                <div class="after-trigger" v-else>
                    <div>
                        <i class="icon-loading"></i>
                    </div>
                </div>
            </div>
        </div>
        <div ref="pulldown" class="ui-scroll__pulldown" v-if="pullDownRefresh" :style="pullDownStyle">
            <div class="before-trigger" v-if="beforePullDown">
                <bubble :y="bubbleY"></bubble>
            </div>
            <div class="after-trigger" v-else>
                <div v-if="isPullingDown" class="loading">
                    <div>
                        <i class="icon-loading"></i>
                    </div>
                </div>
                <div v-else><span>{{refreshTxt}}</span></div>
            </div>
        </div>
    </div>
</template>

<script>
    import BScroll from 'better-scroll';
    import Bubble from '../../bubble';
    const DIRECTION_V = 'vertical';
    const DIRECTION_H = 'horizontal';
    export default {
        name: 'scroll',
        components: {
            Bubble
        },
        props: {
            // refresh
            data: {
                type: Array,
                default: function () {
                    return [];
                }
            },
            // scroll
            /**
            * 1 scroll
            * 2 scroll
            * 3 scrollswipescroll
            */
            probeType: {
                type: Number,
                default: 1
            },
            click: {
                type: Boolean,
                default: true
            },
            // 
            listenScroll: {
                type: Boolean,
                default: false
            },
            // 
            listenBeforeScroll: {
                type: Boolean,
                default: false
            },
            // 
            direction: {
                type: String,
                default: DIRECTION_V
            },
            // 
            scrollbar: {
                type: null,
                default: false
            },
            // 
            pullDownRefresh: {
                type: null,
                default: false
            },
            // 
            pullUpLoad: {
                type: null,
                default: false
            },
            // 
            startY: {
                type: Number,
                default: 0
            },
            // scroll
            refreshDelay: {
                type: Number,
                default: 20
            },
            // 
            freeScroll: {
                type: Boolean,
                default: false
            },
            // mouseWheel
            mouseWheel: {
                type: Boolean,
                default: false
            }
        },
        data () {
            return {
                // 
                beforePullDown: true,
                // bounding
                isRebounding: false,
                // 
                isPullingDown: false,
                // 
                isPullUpLoad: false,
                // 
                pullUpDirty: true,
                // top
                pullDownStyle: '',
                // Y
                bubbleY: 0,
                imgLoading: require('static/img/loading.gif')
            };
        },
        computed: {
            // 
            pullUpTxt () {
                const moreTxt = this.pullUpLoad && this.pullUpLoad.txt && this.pullUpLoad.txt.more;
                const noMoreTxt = this.pullUpLoad && this.pullUpLoad.txt && this.pullUpLoad.txt.noMore;
                return this.pullUpDirty ? moreTxt : noMoreTxt;
            },
            // 
            refreshTxt () {
                return this.pullDownRefresh && this.pullDownRefresh.txt;
            }
        },
        methods: {
            initScroll () {
                if (!this.$refs.wrapper) return;
                // BScroll
                let options = {
                    probeType: this.probeType,
                    click: this.click,
                    scrollY: this.freeScroll || this.direction === DIRECTION_V,
                    scrollX: this.freeScroll || this.direction === DIRECTION_H,
                    scrollbar: this.scrollbar,
                    pullDownRefresh: this.pullDownRefresh,
                    pullUpLoad: this.pullUpLoad,
                    startY: this.startY,
                    freeScroll: this.freeScroll,
                    mouseWheel: this.mouseWheel
                };
                // BScroll
                this.scroll = new BScroll(this.$refs.wrapper, options);
                // scroll
                if (this.listenScroll) {
                    this.scroll.on('scroll', (pos) => {
                        this.$emit('scroll', pos);
                    });
                }
                // beforeScrollStart
                if (this.listenBeforeScroll) {
                    this.scroll.on('beforeScrollStart', () => {
                        this.$emit('beforeScrollStart');
                    });
                }
                // 
                if (this.pullDownRefresh) {
                    this._initPullDownRefresh();
                }
                // 
                if (this.pullUpLoad) {
                    this._initPullUpLoad();
                }
            },
            // BScroll
            disable () {
                this.scroll && this.scroll.disable();
            },
            enable () {
                this.scroll && this.scroll.enable();
            },
            //  better-scroll DOM 
            refresh () {
                this.scroll && this.scroll.refresh();
            },
            // scrollTo(x, y, time, easing)
            scrollTo () {
                this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments);
            },
            // scrollToElement(el, time, offsetX, offsetY, easing)
            scrollToElement () {
                this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments);
            },
            //  better-scroll
            destroy () {
                this.scroll && this.scroll.destroy();
            },
            forceUpdate (dirty) {
                // 
                if (this.pullDownRefresh && this.isPullingDown) {
                    this.isPullingDown = false;
                    this._reboundPullDown().then(() => {
                        this._afterPullDown();
                    });
                } else if (this.pullUpLoad && this.isPullUpLoad) {
                    // 
                    this.isPullUpLoad = false;
                    this.scroll.finishPullUp();
                    this.pullUpDirty = dirty;
                    this.refresh();
                } else {
                    this.refresh();
                }
            },
            _initPullDownRefresh () {
                // 
                this.scroll.on('pullingDown', () => {
                    this.beforePullDown = false;
                    this.isPullingDown = true;
                    this.$emit('pullingDown');
                });
                this.scroll.on('scroll', (pos) => {
                    // bubbleYpullDownStyle
                    if (this.beforePullDown) {
                        this.bubbleY = Math.max(0, pos.y + this.pullDownInitTop);
                        this.pullDownStyle = `top:${Math.min(pos.y + this.pullDownInitTop, 10)}px`;
                    } else {
                        this.bubbleY = 0;
                    }
                    if (this.isRebounding) {
                        this.pullDownStyle = `top:${10 - (this.pullDownRefresh.stop - pos.y)}px`;
                    }
                });
            },
            _initPullUpLoad () {
                // 
                this.scroll.on('pullingUp', () => {
                    this.isPullUpLoad = true;
                    this.$emit('pullingUp');
                });
            },
            _reboundPullDown () {
                const {stopTime = 600} = this.pullDownRefresh;
                return new Promise((resolve) => {
                    setTimeout(() => {
                        this.isRebounding = true;
                        // finishPullDown better-scroll 
                        this.scroll.finishPullDown();
                        resolve();
                    }, stopTime);
                });
            },
            _afterPullDown () {
                // 
                setTimeout(() => {
                    this.pullDownStyle = `top:${this.pullDownInitTop}px`;
                    this.beforePullDown = true;
                    this.isRebounding = false;
                    this.refresh();
                }, this.scroll.options.bounceTime);
            }
        },
        watch: {
            data (val) {
                console.log(val);
                setTimeout(() => {
                    this.forceUpdate(true);
                }, this.refreshDelay);
            }
        },
        created () {
            this.pullDownInitTop = -50;
        },
        mounted () {
            this.$nextTick(() => {
                this.initScroll();
            });
        },
        beforeDestroy () {
            this.destroy();
        }
    };
</script>

<style lang="less" scoped>
    .ui-scroll {
        position: relative;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;

        &__pulldown {
            position: absolute;
            width: 100%;
            left: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: all;
            .after-trigger {
                margin-top: 10px;
                i {
                    font-size: 35px;
                }
            }
        }
        &__pullup {
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 16px 0;
            .after-trigger {
                i {
                    font-size: 35px;
                }
            }
        }
    }
</style>






